@font-face {
    font-family: 'Airbus';
    src:url("./fonts/aispec.ttf");
}

html, body {
  margin: 0;
  padding: 0;
}

:root {
  --colorofchoice: #df09f2;
  --blue: blue;
  --ratio: 33vw;
}

.App {
  font-family: Airbus;
}

.Cloth .menu {
  font-family: Airbus;
  font-size: 30pt;
  /* font-weight: bold; */
  text-shadow: 3pt 3pt black;
}

.Cloth .fun {
  background-color: white;
  display: flex;
  height: inherit;
  width: inherit;
  flex-wrap: wrap;
}

.Cloth img {
  filter: brightness(1.5);
}

.fun .main {
  height: inherit;
  width: var(--ratio);
}
.fun .main div {
  height: 100%;
  width: inherit;
}

.main img {
  bottom: 0px;
  filter: brightness(3);
  width: inherit;
}
.main .eye {
  /* background-color: red; */
  height: 5px;
  left: 308px;
  top: 45%;
  position: fixed;
  width: 5px;
}
.eye.left {
  left: calc(308px - 22px);
}
.eye.right {
  left: calc(308px + 22px);
}

.fun .chooser {
  flex: 2;
  z-index: 200;
}

.chooser img {
  cursor: grab;
  width: calc(100% / 4);
}
.chooser img:active {
  cursor: grabbing;
  /* width: 250px; */
}

.FullText {
  cursor: grabbing;
  font-family: Airbus;
  font-size: 9vw;
  text-align: center;
  text-shadow: 0.8vw 0.4vw black;
}

.black {
  background-color: black;
  position: fixed;
  width: 100%;
  height: 200px;
}

.black:nth-of-type(2) {
  bottom: 0;
}

.App .flipHolder {
  height: 400px;
  width: 100%;
}

.flipperContainer {
  background-color: red;
  position: absolute;
  right: 0;
  width: 80%; /* flipper width */
}

.Flipper {
  /* border: 1px solid black; */
  overflow: hidden;
  position: absolute;
  width: inherit;
}

.Flipper img {
  position: absolute;
  top: 0px;
  height: 100%;
}

.Flipper img:hover {
  clip: none;
}

a, a:visited {
  color:inherit;
}
ul {
  font-family: serif;
  font-size: 40pt;
  list-style-type: upper-roman;
  margin-left: 100px;
}
li div {
  /* background-color: black; */
  /* color:white; */
  text-transform: uppercase;
  color: var(--colorofchoice);
  display:inline-block;
  font-family: Airbus;
  transition-duration: 0.1s;
}

.travText {
}

.main {
  text-shadow: 5px 4px black;
}

.more {
  background-color: white;
  color:black;
  display: none;
  font-family: serif;
  padding-left: 15px;
}

li:hover .more {
  background-color: white;
  color:black;
  display: block;
  /* height: 200px; */
}

li:hover .item::after {
  background-color: white;
  color: black;
  content:'‚è™';
  /* display: block; */
  font-weight: bold;
  letter-spacing: 1px;
  margin-left: 15px;
  x-transition: all 1.4s ease-in;
  animation: ani 2.0s infinite;
}

.Travis {
  background-color: white;
}

.Travis h1 {
  cursor: grabbing;
  font-family: Airbus;
  font-size: 9vw;
  transform: rotate(3deg);
  text-align: center;
  text-shadow: 0.8vw 0.4vw black;
}

.Travis h1 span {
  color: rgba(0,0,0,0.0);
  font-family: Arial;
  font-size: 2vw;
  text-shadow: 0.8vw 0.4vw black;
}

.Travis h2 {
  font-size: 30pt;
  margin-left: 5vw;
  transform: rotate(-0.5deg);
}

.Travis p {
  color:inherit;
  font-family: Arial;
  font-size: 20pt;
  margin: auto 1vw;
  text-align: justify;
  white-space-collapse: discard;
}
.Travis p span {
  font-family: Airbus;
}

.Travis p button {
  background-color: black;
  border:none;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}
.Travis p button:hover {
  cursor:grab;
}
.Travis p button:active {
  cursor: grabbing;
}
.Travis p button:focus {
    outline: 0;
}

.clothContainer {
  height: 200px;
}

@keyframes ani {
  0% {
    margin-left: 15px;
  }
  50% {
    margin-left: 50px;
  }
  100% {
    margin-left: 15px;
  }
}


@media only screen and (max-width: 650px) {

  .Travis h1 {
    transform: rotate(10deg);
    font-size: 19vw;
    text-shadow: 1.6vw 0.8vw black;
  }

  .Travis p {
    /* font-size: 6vw; */
    margin: auto 3vw;
  }
  .Cloth .fun {
    display: block;
  }
  .Cloth .main {
    width: 100%;
  }

  .Cloth .chooser {
  }

  ul {
    font-size: 20pt;
    margin-left: 20px;
  }
  .main {
    text-shadow: 3px 2px black;
  }
  .Flipper {
    border: 2px solid lightgreen;
  }
}
